import React, {useState, useEffect} from 'react'
import {Container} from 'react-bootstrap'
import { Row, Col, Button, Tabs, Mentions, Input} from "antd";
import { Link } from 'react-router-dom'
import domtoimage from 'dom-to-image';
import { useDispatch, useSelector } from 'react-redux';
import { setCharacter, toggleLinked } from '../_actions/actions';
import html2canvas from 'html2canvas';
import {CopyToClipboard} from 'react-copy-to-clipboard';

const { TabPane } = Tabs;
const { TextArea } = Input;

/*-- 
SPDX-FileCopyrightText: © 2021 Hyun Uk Lee <as0266@naver.com>

SPDX-License-Identifier: MIT
--*/ 

function NavBar() {

    const dispatch = useDispatch();

    const Linked = useSelector(state => state.linked)
    const info = useSelector(state => state.info)
    
    const [RealLink, setRealLink] = useState(Linked)
    const [Data, setData] = useState("")

    const handleSaveClick = () => {
        const varUA = navigator.userAgent.toLowerCase();
        if ( varUA.indexOf("iphone") > -1||varUA.indexOf("ipad") > -1||varUA.indexOf("ipod") > -1 ) {
            console.log("html2canvas 실행됨");
            html2canvas(document.querySelector('.checker'), {
                onrendered: function (canvas) {
                    let a = document.createElement('a');
                    a.href = canvas.toDataURL("image/jpeg").replace("image/jpeg", "image/octet-stream")
                    a.download = `character${RealLink ? "_result" : ""}.png`;
                    a.click();
                }
            })
        } else {
            domtoimage.toJpeg(document.querySelector('.checker'), { quality: 1 })
            .then(function (dataUrl) {
               var link = document.createElement('a');
               link.download = `character${RealLink ? "_result" : ""}.png`;
               link.href = dataUrl;
               link.click();
           });
        }
        
    }

    const toggleLink = () => {
        const newLinked = !Linked;
        dispatch(toggleLinked(newLinked))
        window.localStorage.setItem("linked", newLinked)
        setRealLink(newLinked)
    };


    const onChange = (e) => {
        setData(e.target.value)
    }

    const loadData = () => {
        window.localStorage.setItem("characterinfo", Data)
        dispatch(setCharacter(Data))
        alert("데이터 로드에 성공하였습니다.")
        window.location.reload();
    }

    return (
        <Container fluid style={{ maxWidth:"1200px", textAlign: "center", margin:"1rem auto"}}>
            <Row justify="center" align="middle" style={{marginBottom: "20px"}}>
                <Col xs={12} md={4}>
                    <div className="buttonbar">
                        <Button type="primary" onClick={handleSaveClick} style={{margin:"1rem"}}>Download</Button>
                    </div>
                </Col>
                <Col xs={12} md={4}>
                    <Link style={{textDecoration: "none"}} to={`/${RealLink ? "" : "result"}`}><div onClick={toggleLink}
                    style={{backgroundColor: "gray", color: "white", fontWeight: 600, height: "35px",
                    lineHeight: "35px", borderRadius: "5px"}}>
                        {RealLink ? "체크표" : "결과"}
                    </div></Link> 
                </Col>
                <Col xs={24} md={16}>
                    <b>
                    4성 이하 캐릭터는 제공되지 않습니다.<br/>
                    루나틱 캐릭터를 제외하고, <br/>
                    보드의 속성을 기준으로 분류합니다.<br/>
                    ex) 마리엘-바람, 엘가-땅, 신시아-바람
                    </b>
                </Col>
            </Row>
            <Row justify="center">
                <Col span={20} style={{marginBottom: "30px"}}>
                    <Tabs type="card">
                        <TabPane tab="데이터 복사" key="1">
                            <Mentions style={{ width: '70%', height: "40px", marginRight: "20px" }} placeholder={JSON.stringify(info)} readOnly />
                            <CopyToClipboard text={JSON.stringify(info)}
                                onCopy={() => alert("체크표 데이터가 복사되었습니다.")}>
                                <Button style={{ height: "40px"}}>Copy</Button>
                            </CopyToClipboard>
                            
                        </TabPane>
                        <TabPane tab="데이터 로드" key="2">
                            <TextArea style={{ width: '70%', height: "40px", marginRight: "20px" }} value={Data} onChange={onChange} placeholder="가지고 있는 데이터를 입력하세요"/>
                            <Button style={{ height: "40px"}} onClick={loadData}>Load</Button>
                        </TabPane>
                    </Tabs>
                </Col>
            </Row>
        </Container>
    )
}

export default NavBar
